<template>
    <div class="lodin">
        <mt-spinner :type="lodinType" :color="lodinColor" :size="lodinSzie"></mt-spinner>
        <span>{{contentText}}</span>
    </div>
</template>

<script>
export default {
  props: {
    lodinType: {
      type: String,
      default: "snake"
    },
    lodinColor: {
      type: String,
      default: "rgb(255, 120, 179)"
    },
    lodinSzie: {
      type: Number,
      default: 26
    },
    contentText: {
      type: String,
      default: "正在加载"
    }
  }
};
</script>

<style scoped lang="less">
.lodin {
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  span {
    color: #999;
    font-size: 28px;
    padding-left: 12px;
  }
}
</style>


