<template>
    <mt-header :title="title" class="header">
        <router-link to="/" slot="left">
            <mt-button icon="back">返回</mt-button>
        </router-link>
        <mt-button icon="more" slot="right"></mt-button>
    </mt-header>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: "首页",
      required: false
    }
  }
};
</script>

<style scoped lang="less">
.header {
  position: static;
  background: rgb(255, 120, 179);
  height: 100px;
}
</style>


