<template>
    <div class="news-item">
        <header class="header">
            <div class="avatar">
                <router-link to="/">
                    <img :src="data.upic" alt="">
                </router-link>
            </div>
            <div class="userinfo font-class">
                <h3>{{data.username}}</h3>
                <p>
                    <span class="time">{{data.pubtime}}</span>
                    <span class="type">{{data.carType}}</span>
                </p>
            </div>
            <div class="more">
                <i class="iconfont icon-down"></i>
            </div>
        </header>
        <main class="main">
            <p>{{data.content}}</p>
            <ul class="clearfix">
                <li v-for="(item,index) in data.photos" :key="index" @click="click(item.src)">
                    <img :src="item.src" :alt="item.alt">
                </li>
            </ul>
        </main>
        <transition
        enter-active-class="fadeDot3 fadeInUp" 
        leave-active-class="fadeDot3 fadeOutDown"
        >
            <div class="model" v-if="status" @click="status=!status">
                <img :src="model">
            </div>
        </transition>
        <footer class="footer">
            <div class="info">
                <p><em>{{data.like}}赞</em><em>{{data.comment}}评论</em></p>
            </div>
            <div class="btn-s">
                <i :class="['iconfont icon-thumbsup',{isthumbsup:isThumbsup}]" @click="thumbsups"></i>
                <i :class="['iconfont icon-aui-icon-comment']"></i>
                <i :class="['iconfont icon-share']"></i>
            </div>
        </footer>
    </div>
</template>

<script>
export default {
  props: {
    data: Object
  },
  data() {
    return {
      model: "",
      status: false,
      isThumbsup: false
    };
  },
  methods: {
    click(value) {
      this.model = value;
      this.status = true;
    },
    thumbsups() {
      this.isThumbsup = !this.isThumbsup;
      if (this.isThumbsup === false) {
        this.data.like--;
      }

      if (this.isThumbsup === true) {
        this.data.like++;
      }
    }
  }
};
</script>

<style scoped lang="less">
.news-item {
  width: 100%;
  height: auto;
  //   background: #ccc;
  padding: 20px;
  border-bottom: 30px solid #ddd;
  .header {
    display: flex;
    align-items: center;
    height: 100px;
    .avatar {
      width: 66px;
      height: 66px;
      background: #ccc;
      border-radius: 50%;
      a {
        display: block;
        img {
          display: block;
          width: 100%;
          height: 100%;
          border-radius: 50%;
        }
      }
    }
    .userinfo {
      flex: 1;
      padding-left: 20px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      h3 {
        font-size: 32px;
        font-weight: normal;
        color: #333;
      }
      p .time {
        font-size: 20px;
        color: #999;
      }
      p .type {
        font-size: 20px;
        color: #ff85b9;
      }

      h3,
      p {
        margin: 0;
      }
    }
    .more {
      display: flex;
      align-items: flex-start;
      height: inherit;
      // font-size: 18px;
      .icon-down {
        font-size: 14px;
      }
    }
  }
  .main {
    min-height: 220px;
    p {
      font-size: 28px;
      color: #333333;
      margin: 10px 0;
      line-height: 40px;
    }
    ul {
      padding: 0;
      li {
        font-size: 0;
        width: 220px;
        height: 220px;
        margin: 5px 0;
        float: left;
        padding: 0 3px;
        box-sizing: content-box;
        img {
          width: inherit;
          height: inherit;
        }
      }
    }
  }
  .model {
    width: 100%;
    height: 100%;
    background: #000;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    img {
      position: absolute;
      width: 100vw;
      top: 50%;
      margin-top: -50vw;
    }
  }
  .footer {
    height: 100px;
    width: 100%;
    //   background: #ccc;
    display: flex;
    margin-top: 20px;
    .info {
      flex: 1;
      p {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        height: 100%;
        color: #999;
        font-size: 28px;
      }
    }
    .btn-s {
      width: 284px;
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: space-between;
      i {
        width: 44px;
        height: 44px;
        cursor: pointer;
        // background: rgb(204, 204, 204);
        border-radius: 22px;
        text-align: center;
        line-height: 44px;
        color: #999;
        font-size: 40px;
      }
      .iconfont.isthumbsup {
        color: #ff85b9;
      }
    }
  }
}
.news-item:last-child {
  border-bottom: none;
}
.fadeDot3 {
  animation-duration: 0.6s;
}
</style>


