<template>
    <mt-swipe class="swipe" :auto="4000">
        <mt-swipe-item v-for="(item, index) in data" :key="index">
            <img :src="item.src" :alt="item.alt">
        </mt-swipe-item>
    </mt-swipe>
</template>

<script>
export default {
  props: {
    data: {
      type: Array,
      required: true
    }
  },
  data() {
    return {};
  }
};
</script>

<style scoped lang="less">
.swipe {
  height: 142px;
  width: 100%;
  overflow: hidden;
  img {
    width: 100%;
    height: 100%;
  }
}
</style>


